@media only screen and (max-width: 768px) {
  .bodyContainer {
    grid-template-columns: 100%;
    grid-template-rows: 50% 50%;
    row-gap: 150px;
  }

  .col1 {
    grid-row: 1/2;
  }

  .col2 {
    grid-row: 2/3;
  }
}

@media only screen and (max-width: 425px) {
  .navLinks,
  .addressBar,
  #homeNav {
    display: none;
  }

  .resNavBtn {
    display: block;
  }

  .bodyContainer {
    row-gap: 200px;
  }
}
@media (max-width: 575.98px) {
  .chart {
    margin-top: 170vh;
  }
  .resNavBtn {
    display: block;
    color: white;
    filter: invert(1);
  }
  .dailYreward {
    position: absolute;
    width: 80%;
    top: 95vh;
  }
  .clock {
    position: absolute;
    top: 158vh;
    left: 4%;
    width: 95%;
  }
  .bodyContainer {
    row-gap: 0;
    display: grid;
    grid-template-columns: 1fr;
    display: flex;
    flex-direction: column;
  }
}
.headdingButton {
  position: absolute;
  background: transparent;
  border: 1px solid #58f558;
  color: white;
  /* padding: 43vh 0px; */
  top: 16vh;
  padding: 10px 20px;
  border-radius: 30px;
  transition: 0.5s ease-in-out;
  left: 7%;
}

.headdingButton:hover {
  background-color: #58f558;
  color: white;
}
@media (min-width: 768px) {
  .headdingButton {
    display: none;
  }
}
@media (max-width: 575.98px) {
  .headdingButton {
    display: inline-block;
  }
}
