/* Body //////////////////////////////////// */

.bodyContainer {
  min-height: 100vh;
  background: #0b0b0b;
  display: grid;
  grid-template-columns: 60% 35%;
  column-gap: 50px;
  padding: 50px 30px;
}

.col1 {
  /* border: 2px solid green; */
  /* grid-column: 1/2; */
}

.col2 {
  /* border: 2px solid red; */
  /* grid-column: 2/3; */
  gap: 50px;
}

.chart {
  background: #151515;
  padding: 20px;
  border-radius: 12px;
}

.chart h1 {
  color: white;
}

.card__top {
  background: white;
  color: black;
}

.flip-countdown.size-medium .flip-countdown-piece .flip-countdown-title {
  color: white;
}

.clock {
  padding: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
}
@media (max-width: 575.98px) {
  .clock {
    padding: 50px 0px;

    gap: 20px;
  }
}
.dailYreward {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  padding-bottom: 80px;
}

.dailYreward h3 {
  color: white;
}

.dailYbutton {
  margin-top: 80px;
}

.dailyrewardButton {
  width: 130px;
  height: 130px;
  border-radius: 50%;
  background: #232ae5;
  position: relative;
  box-shadow: 0 0 60px 10px #232ae5;
  display: block;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-family: 'Quicksand', sans-serif;
  font-size: 30px;
  color: white;
  font-weight: 700;
}

.dailyrewardButton::before {
  content: '';
  position: absolute;
  inset: 0;
  border: 1px solid #232ae5;
  border-radius: 50%;
  animation-name: pulse;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  box-shadow: 0 0 10px 2px #232ae5;
}

.dailyrewardButton::after {
  content: '';
  position: absolute;
  inset: 0;
  border: 1px solid #232ae5;
  border-radius: 50%;
  animation-name: pulse;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-delay: 1s;
  box-shadow: 0 0 10px 2px #232ae5;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0;
  }

  10% {
    opacity: 1;
  }

  100% {
    transform: scale(2.3);
    opacity: 0;
  }
}

.manageMoney {
  padding: 15px;
  background: #041564;
  border-radius: 12px;
  color: white;
  margin: auto;
}

.topSide,
.bottomSide {
  padding: 20px;
}

.manageMoney hr {
  border: #1d2c73 0.5px solid;
}

#totleBalance {
  display: flex;
  align-items: center;
  color: #78787e;
}

.register {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bottomSide p {
  color: #78787e;
  font-size: 18px;
}

.totleBalance,
.register,
.bottomSide p {
  padding: 10px;
}

#registerBtn {
  background-color: #4bdb4b;
  border: 1px solid #4bdb4b;
  color: #041564;
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  padding: 8px 15px;
  border-radius: 25px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-text-decoration: none;
  text-decoration: none;
  font-family: 'Source Sans Pro', sans-serifs;
}

.stackingState {
  background: #151515;
  margin: 40px 0 40px 0;
  color: white;
  border-radius: 10px;
}

.stackingState hr {
  border: #2c2c2c 0.5px solid;
}

.bottomSideStack,
.topSideStack {
