  font-size: 17px;
  font-weight: 300;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-text-decoration: none;
  text-decoration: none;
  margin-right: 10px;
}

table {
  width: 100%;
}

table td {
  border: none !important;
  padding: 10px;
}

#refral_input {
  padding: 10px;
  border-radius: 15px;
  width: 120px;
  margin-top: 10px;
  background-color: black;
  border: 1px solid #4bdb4b;
  color: white;
}

#refral_input::placeholder {
  color: #4bdb4ba3;
}

#refral_input:focus {
  outline: none;
}

@media (max-width: 575.98px) {
  .manageMoney {
    background: #041564;
    border-radius: 12px;
    color: white;
   
    width: 90%;
  }
  .bodyContainer{
    min-height: fit-content;
  }
  .chart{
    margin-top: 10px !important;
  }
  #walletBtnMobile{
    display: block;
    margin-bottom: 45px;
  }
  #walletBtnPc{
    display: none;
  }
  .dailYreward{
    width: 100% !important;
    position: static !important;
  }
  .clock{
    position: static !important;
  }
}
@media (max-width: 767.98px) {
  .resNav {
    position: absolute;
    width: 100%;
    height: 50vh;
    top: 12%;
    left: 0;
    z-index: 9898979745464989;
    background: #0b0b0b;
    transition: height 0.5s ease-in-out;
  }
}
.resNav {
  padding: 0px 10px;
}
.resNav ul {
  padding-top: 30px;
}
.resNav ul li {
  list-style: none;
  padding-bottom: 15px;
  color: white;
}
